.meetingsBackground {
    background-color: rgba(119, 121, 128, 0.966);
    max-width: 936px;
}
.mapBackground {
    background-color: rgb(0, 0, 0);
    border-radius: 10px;
    color: whitesmoke;
    max-width: 250px;
    box-shadow: 2px 2px 7px 1px #c5c5c5c2;
}
@media (max-width: 767.98px) {
    .mapBackground {
        max-width: none;
        width: 100%;
    }
}